<template>
  <div id="new-thing">
    <v-layout row>
      <v-flex xs12 sm8 offset-sm2>
        <v-form ref="form">
            <v-text-field
              label="Name"
              v-model="name"
              :counter="30"
            ></v-text-field>
              <v-select
                :items="categories"
                v-model="category"
                label="Category"
                autocomplete
              ></v-select>
            <v-text-field
              label="Location"
              v-model="location"
            ></v-text-field>
            <v-text-field
              label="Description"
              v-model="description"
            ></v-text-field>
            <v-layout row mb-3>
              <v-flex xs12>
                <v-tooltip bottom>
                  <v-btn raised class="primary" @click="onPickFile" slot="activator">Upload image</v-btn>
                  <!-- <v-btn raised class="primary" @click="$refs.fileInput.click()" slot="activator">Pick file</v-btn>
                  <v-btn raised class="primary" @click="onUpload" slot="activator">Upload image</v-btn> -->
                  <span>Size should be less than 1Mb</span>
                </v-tooltip>
                <input 
                  type="file" 
                  ref="fileInput"
                  style="display: none"
                  accept="image/*"
                  @change="onFilePicked">
              </v-flex>
            </v-layout>
            <v-layout row>
              <v-flex xs12>
                <img :src="image" class="image-preview">
              </v-flex>
            </v-layout>
            <v-btn color="success"
              @click="saveThing"
            >
              submit
            </v-btn>
            <router-link to="/" class="card-btn">  
              <v-btn outline color="primary">Back</v-btn>
            </router-link>
          </v-form>
        </v-flex>
      </v-layout>  
  </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
// import axios from 'axios'

export default {
  name: 'new-thing',
  data () {
    return {
      thing_id: null,
      name: null,
      description: null,
      location: null,
      image: null,
      // selectedFile: null,
      category: null,
      categories: [],
      creatorID: null,
    }
  },
  methods: {
    // test
    // onFileSelected (event) {
    //   this.selectedFile = event.target.files[0]
    // },
    // onUpload (){
    //   const fd = new FormData;
    //   fd.append('image', this.selectedFile, this.selectedFile.name);
    //   axios.post("https://us-central1-know-what-you-have.cloudfunctions.net/uploadFile", fd)
    //     .then(res => {
    //       console.log(res)
    //     })
    // },
    saveThing () {
      var user = firebase.auth().currentUser;

      var thing = {
        thing_id: this.generateUUID(),
        name: this.name,
        description: this.description,
        location: this.location,
        image: this.image,
        category: this.category,
        creatorID: user.uid
      }
      this.$store.dispatch('createThing', thing).then(docRef => this.$router.push('/'))
    },
    generateUUID () {
       return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
      });
    },
    onPickFile () {
      this.$refs.fileInput.click();
    },
    onFilePicked (e) {
      const files = e.target.files
      let filename = files[0].name
      if(filename.lastIndexOf('.') <= 0) {
        alert("Please add a valid file")
      }
      const fileReader = new FileReader();
      fileReader.addEventListener('load', () => {
        this.image = fileReader.result;
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]
    }
  },
  created () {
    db.collection('categories').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'name': doc.data().name
        }
        this.categories.push(data.name)
      })
    })
  }
}
</script>

<style scoped>

a{
  text-decoration: none;
}

.list__tile--link{
  position: relative;
}

.list__tile--link:after{
  position: absolute;
  right: 0;
  content: "X"
}

.image-preview{
  width: 100%;
}
</style>




