import Vue from 'vue'
import Vuex from 'vuex'
import db from '../components/firebaseInit'
import * as firebase from 'firebase'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    user: null,
    loading: false,
    error: null,
    things: []
  },
  getters: {
    error (state) {
      return state.error
    },
    loading (state) {
      return state.loading
    },
    things (state) {
      return state.things
    }
  },
  mutations: {
    setLoadedThing (state, payload){
      state.things = payload
    },
    setLoading (state, payload){
      state.loading = payload
    },
    setError (state, payload){
      state.error = payload
    },
    clearError (state){
      state.error = null
    },
    createThing (state, payload){
     state.things.push(payload) 
    }
  },
  actions: {
    changeLoading({commit}, payload){
      commit("setLoading", payload)
    },
    loadThings ({commit}){
      var userId = firebase.auth().currentUser.uid;
      
      commit("setLoading", true)
     
      db.collection('things').where("creatorID", "==", userId).get().then(querySnapshot => {
        const things = []
        querySnapshot.forEach(doc => {
          const data = {
            'id': doc.id,
            'thing_id': doc.data().thing_id,
            'name': doc.data().name,
            'description': doc.data().description,
            'location': doc.data().location,
            'category': doc.data().category,
            'image': doc.data().image
          }
          things.push(data)
        })
        commit('setLoadedThing', things)
        commit("setLoading", false)
      })
      .catch(err => {
        console.log(err)
        commit("setLoading", false)
      })
    },
    createThing ({commit}, payload){
      var thing = {
        thing_id: payload.thing_id,
        name: payload.name,
        description: payload.description,
        location: payload.location,
        image: payload.image,
        category: payload.category,
        creatorID: payload.creatorID
      }

      db.collection('things').add(thing).then((data)=>{
        //delete later
        console.log(data)
      })
      .catch((error) => {
        console.log(error)
      })

      commit('createThing', thing)
    },
    registerUser ({commit}, payload){
      commit('clearError')
      commit('setLoading', true)
      firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
        .then(user => {
          commit('setLoading', false)
          commit('setUser', user)
        },
        err => {
          commit('setLoading', false)
          commit('setError', err)
          console.error(err.message)
        }
      );
    },
    logInUser ({commit}, payload){
      commit('clearError')
      commit('setLoading', true)
      firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
      .then(user => {
        commit('setLoading', false)
        commit('setUser', user)
      },
      err => {
        commit('setLoading', false)
        commit('setError', err)
        console.error(err.message)
      }
    )},
    clearError ({commit}){
      commit('clearError')
    }
  }

})




