<template>
    <v-alert type="error" dismissible @input="onClose" :value="true">
        {{text}}
    </v-alert>
</template>

<script>
export default {
    props: {
        text: String
    },
    methods: {
        onClose () {
            this.$emit('dismissed')
        }
    }
}
</script>



