<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <!-- error messanges -->
            <vue-alert 
              @dismissed="onDismissed" 
              :text="error.message" 
              v-if="error" 
              class="error-banner">
            </vue-alert>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Login form</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form @submit.prevent="login">
                  <v-text-field 
                    prepend-icon="person" 
                    name="login" 
                    label="Login" 
                    type="text"
                    v-model="email"
                    >
                  </v-text-field>
                  <v-text-field 
                    prepend-icon="lock" 
                    name="password" 
                    label="Password" 
                    id="password" 
                    type="password"
                    v-model="password"
                    >
                  </v-text-field>
                   <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn 
                  type="submit"
                  color="primary"
                  :loading="loading"
                  :disabled="loading"
                  >Sign in
                  </v-btn>
                </v-card-actions>
                </v-form>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import firebase from 'firebase';

  export default {
    name: 'login',
    data: () => ({
      drawer: null,
      email: '',
      password: ''
    }),
    computed: {
      user () {
        return this.$store.getters.user
      },
      error () {
        return this.$store.getters.error
      },
      loading () {
        return this.$store.getters.loading
      }
    },
    watch: {
      user(value){
        if(value !== null && value !== undefined){
          this.$router.go({path: this.$router.path});
        }
      }
    },
    methods: {
      login () {
        this.$store.dispatch('logInUser', {email: this.email, password: this.password})
      },
      onDismissed () {
        this.$store.dispatch('clearError')
      }
    }
  }
</script>





