<template>
  <div>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-card-text class="text-md-center">  
          <h1 class="main-header">Things list:</h1>
          <span class="main-header-span">You have a lot. Don't forget about it!</span>
        </v-card-text>
        <div class="mt-5 cards-wrapper">
            <!-- iteration throught things -->
            <!-- <template v-for="(thing, index) in things">
              <router-link class="thing-show" :key="thing.index" :to="{name: 'view-thing', params: {thing_id: thing.thing_id}}">
                <v-list-tile avatar :key="thing.index" class="thing-item">
                  <v-list-tile-action>{{index+1}}</v-list-tile-action>
                  <v-list-tile-avatar>
                    <img :src="thing.image">
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title v-html="thing.name"></v-list-tile-title>
                    <v-list-tile-sub-title v-html="thing.description"></v-list-tile-sub-title>
                    <v-list-tile-sub-title v-html="thing.location"></v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
              </router-link>
            </template> -->
          <div class="search-input-wrapper">
            <v-text-field
              label="Search here for a thing ... "
              v-model="search"
              append-icon="search"
              solo 
              hide-details 
              single-line
            ></v-text-field>
          </div>

          <v-card v-if="!loading">
            <v-container
              fluid
              style="min-height: 0;"
              grid-list-lg
            >
            <template v-for="thing in filteredList">
              <router-link class="thing-show" :key="thing.index" :to="{name: 'view-thing', params: {thing_id: thing.thing_id}}">
                <v-layout row wrap>
                  <v-flex xs12>
                    <v-card color="cyan darken-1" class="white--text">
                      <v-container fluid grid-list-lg>
                        <v-layout row>
                          <v-flex xs7>
                            <div>
                              <div class="headline">{{thing.name}}</div>
                              <div class="card-info" v-if="thing.category">Category: {{thing.category}}</div>
                              <div class="card-info" v-if="thing.location">Location: {{thing.location}}</div>
                              <div class="card-info" v-if="thing.description">Description: {{thing.description}}</div>
                            </div>
                          </v-flex>
                          <v-flex xs5>
                            <v-card-media
                              :src="thing.image"
                              height="125px"
                              contain
                            ></v-card-media>
                          </v-flex>
                        </v-layout>
                      </v-container>
                    </v-card>
                  </v-flex>
                </v-layout>
              </router-link>
            </template> 
            </v-container>
          </v-card>
        </div>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular 
          indeterminate 
          :size="100" 
          color="primary"
          v-if="loading"
          >
        </v-progress-circular>
      </v-flex>
    </v-layout>
    <!-- add new thing btn -->
    <router-link to="/new">  
      <v-btn fab dark color="red" class="add-thing-btn">
        <v-icon dark>add</v-icon>
      </v-btn>
    </router-link>
  </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'

export default {
  data(){
    return {
      search: ''
    }
  },
  computed: {
    things () {
      return this.$store.getters.things
    },
    loading () {
      return this.$store.getters.loading
    },
    filteredList() {
      return this.things.filter(thing => {
        return thing.name.toLowerCase().includes(this.search.toLowerCase())
      })
    }
  },
  created () {      
    this.$store.dispatch('loadThings')
  }
}
</script>

<style scoped>

  .main-header{
    font-family: 'Exo', sans-serif;
    font-size: 56px;
  }

  .main-header-span{
    color: gray;
    font-family: 'Exo', sans-serif;
    letter-spacing: 1px;
    font-family: 16px;
  }

  .add-thing-btn{
    position: fixed;
    bottom: 5%;
    right: 3%;
  }

  .thing-show{
    text-decoration: none;
  }

  .thing-item:hover{
    background: #fafafa;
  }

  .search-input-wrapper{
    margin-bottom: 15px;
  }

  .card-info{
    font-style: italic;
  }
</style>




