<template>
  <v-app>
    <!-- navigation component -->
    <navigation app></navigation>
    <!-- sidebar -->
    <v-content>
      <v-container fluid>
          <!-- render views -->
          <router-view/>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>

import Navigation from './components/Navigation.vue'

export default {
  name: 'App',
  data () {
    return {
      drawer: false
    }
  },
  components: {
    Navigation
  }
}
</script>


<style>
/* btn spinner */
.custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .error-banner{
    margin-bottom: 0px;
  }
</style>



